import { Link } from 'react-router-dom';
import useDrive from '../../../hooks/drive/useDrive';
import { DriveGrant } from '../../../provider/connections/CirlceDataTypes';
import { drivePermissionLevels } from '../../../provider/permission/permissionLevels';
import HardDrive from '../../Icons/HardDrive/HardDrive';
import { getAccessFromPermissionNumber } from '../../Templates/DemoData/helpers';

const DrivePermissionView = ({
  driveGrant,
  permissionTree,
  className,
}: {
  driveGrant: DriveGrant;
  permissionTree?: string;
  className?: string;
}) => {
  const { data: drive } = useDrive({ targetDrive: driveGrant.drive }).fetch;

  return (
    <div
      key={`${driveGrant.drive.alias}-${driveGrant.drive.type}`}
      className={`flex flex-row ${className}`}
      title={permissionTree}
    >
      <Link
        to={`/owner/drives/${drive?.targetDriveInfo?.alias}_${drive?.targetDriveInfo?.type}`}
        className="contents"
      >
        <HardDrive className="mt-1 mb-auto mr-3 h-6 w-6" />
        <div className="flex flex-col">
          <p className={`leading-none ${!permissionTree ? 'my-auto' : ''}`}>
            {drive?.name}:{' '}
            {getAccessFromPermissionNumber(driveGrant.permission, drivePermissionLevels).name}
          </p>
          {permissionTree && <small className="ml-1">{permissionTree}</small>}
        </div>
      </Link>
    </div>
  );
};

export default DrivePermissionView;
