import { useQuery } from '@tanstack/react-query';
import { CircleNetworkProvider } from '../../provider/connections/CircleNetworkProvider';
import { CircleNetworkRequestProvider } from '../../provider/connections/CircleNetworkRequestProvider';
import useAuth from '../auth/useAuth';

const useConnections = () => {
  const { getSharedSecret } = useAuth();
  const circleNetworkRequestProvider = CircleNetworkRequestProvider.getInstance(getSharedSecret());
  const circleNetworkProvider = CircleNetworkProvider.getInstance(getSharedSecret());

  const fetchPendingConnections = async () => {
    return await (
      await circleNetworkRequestProvider.getPendingRequests({ pageNumber: 1, pageSize: 10 })
    ).results;
  };

  const fetchSentRequests = async () => {
    return await (
      await circleNetworkRequestProvider.getSentRequests({ pageNumber: 1, pageSize: 10 })
    ).results;
  };

  const fetchConnections = async () => {
    return await (
      await circleNetworkProvider.getConnections({ pageNumber: 1, pageSize: 10 })
    ).results;
  };

  return {
    fetchPending: useQuery(['pendingConnections'], () => fetchPendingConnections(), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
    fetchSent: useQuery(['sentRequests'], () => fetchSentRequests(), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
    fetchActive: useQuery(['activeConnections'], () => fetchConnections(), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),
  };
};

export default useConnections;
