import { ReactNode } from 'react';
import { Link } from 'react-router-dom';

const PersonCard = ({
  name,
  href,
  className,
  children,
  onClick,
}: {
  name: string;
  href?: string;
  className: string;
  children: ReactNode;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
}) => {
  const Wrapper = ({ children }) => (href ? <Link to={href}>{children}</Link> : <>{children}</>);

  return (
    <div className={className}>
      <Wrapper>
        <div
          className={`h-full rounded-md border-[1px] border-gray-200 border-opacity-60 bg-white transition-colors dark:border-gray-800 dark:bg-gray-800`}
          onClick={onClick}
        >
          <div className="aspect-square">
            <img src="https://placehold.co/200" className="h-full w-full" />
          </div>
          <div className="p-2">
            <h2 className="font-thiner mb-6 dark:text-white">{name}</h2>
            {children}
          </div>
        </div>
      </Wrapper>
    </div>
  );
};

export default PersonCard;
