import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { CircleMembershipProvider } from '../../provider/connections/CircleMembershipProvider';
import { CircleProvider } from '../../provider/connections/CircleProvider';
import useAuth from '../auth/useAuth';

const useCircle = ({ circleId }: { circleId: string }) => {
  const queryClient = useQueryClient();

  const { getSharedSecret } = useAuth();
  const circleProvider = CircleProvider.getInstance(getSharedSecret());
  const circleMembershipProvider = CircleMembershipProvider.getInstance(getSharedSecret());

  const fetch = async ({ circleId }: { circleId: string }) => {
    return await circleProvider.getCircle(circleId);
  };

  const fetchMembers = async ({ circleId }: { circleId: string }) => {
    return await circleMembershipProvider.fetchMembers(circleId);
  };

  const provideGrants = async ({ dotYouIds }: { dotYouIds: string[] }) => {
    return await Promise.all(
      dotYouIds.map(
        async (dotYouId) =>
          await circleMembershipProvider.add({ circleId: circleId, dotYouId: dotYouId })
      )
    );
  };

  const revokeGrants = async ({ dotYouIds }: { dotYouIds: string[] }) => {
    return await Promise.all(
      dotYouIds.map(
        async (dotYouId) =>
          await circleMembershipProvider.remove({ circleId: circleId, dotYouId: dotYouId })
      )
    );
  };

  const provideGrant = async ({ dotYouId }: { dotYouId: string }) => {
    return await circleMembershipProvider.add({ circleId: circleId, dotYouId: dotYouId });
  };

  const revokeGrant = async ({ dotYouId }: { dotYouId: string }) => {
    return await circleMembershipProvider.remove({ circleId: circleId, dotYouId: dotYouId });
  };

  return {
    fetch: useQuery(['circle', circleId], () => fetch({ circleId }), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),

    fetchMembers: useQuery(['cirleMembers', circleId], () => fetchMembers({ circleId }), {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
    }),

    provideGrants: useMutation(provideGrants, {
      onSuccess: async (data, param) => {
        queryClient.invalidateQueries(['circles']);
        queryClient.invalidateQueries(['circle', circleId]);
        queryClient.invalidateQueries(['cirleMembers', circleId]);
        await Promise.all(
          param.dotYouIds.map(async (dotYouId) => {
            return await queryClient.invalidateQueries(['connectionInfo', dotYouId]);
          })
        );
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),

    revokeGrants: useMutation(revokeGrants, {
      onSuccess: async (data, param) => {
        queryClient.invalidateQueries(['circles']);
        queryClient.invalidateQueries(['circle', circleId]);
        queryClient.invalidateQueries(['cirleMembers', circleId]);
        await Promise.all(
          param.dotYouIds.map(async (dotYouId) => {
            return await queryClient.invalidateQueries(['connectionInfo', dotYouId]);
          })
        );
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),

    provideGrant: useMutation(provideGrant, {
      onSuccess: async (data, param) => {
        queryClient.invalidateQueries(['circles']);
        queryClient.invalidateQueries(['circle', circleId]);
        queryClient.invalidateQueries(['cirleMembers', circleId]);
        queryClient.invalidateQueries(['connectionInfo', param.dotYouId]);
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),

    revokeGrant: useMutation(revokeGrant, {
      onSuccess: async (data, param) => {
        queryClient.invalidateQueries(['circles']);
        queryClient.invalidateQueries(['circle', circleId]);
        queryClient.invalidateQueries(['cirleMembers', circleId]);
        queryClient.invalidateQueries(['connectionInfo', param.dotYouId]);
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),
  };
};

export default useCircle;
