import { useState } from 'react';
import { createPortal } from 'react-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useConnections from '../../../hooks/connections/useConnections';
import usePortal from '../../../hooks/portal/usePortal';
import ActionButton, { ActionButtonState } from '../../Buttons/ActionButton';
import PersonCard from '../../Connection/PersonCard/PersonCard';
import Checkbox from '../../Form/Checkbox';
import DialogWrapper from '../Common/DialogWrapper';

const MemberLookupDialog = ({
  title,
  confirmText,

  isOpen,
  defaultMembers,
  confirmationStatus,

  onConfirm,
  onCancel,
}: {
  title: string;
  confirmText?: string;

  isOpen: boolean;
  defaultMembers: string[];
  confirmationStatus: ActionButtonState;

  onConfirm: (toProvide: string[], toRevoke: string[]) => void;
  onCancel: () => void;
}) => {
  const target = usePortal('modal-container');
  const { data: members, isLoading: membersLoading } = useConnections().fetchActive;

  const [toProvideMembers, setToProviderMembers] = useState<string[]>([]);
  const [toRevokeMembers, setToRevokeMembers] = useState<string[]>([]);

  if (!isOpen) {
    return null;
  }

  const toggleMember = (checked: boolean, dotYouId: string) => {
    if (checked) {
      // If member wasn't there, add to provide
      if (!defaultMembers.some((defaultMember) => defaultMember === dotYouId)) {
        setToProviderMembers([...toProvideMembers, dotYouId]);
      }
      setToRevokeMembers(toRevokeMembers.filter((defaultMember) => defaultMember !== dotYouId));
    } else {
      // If member was there, add to revoke
      if (defaultMembers.some((defaultMember) => defaultMember === dotYouId)) {
        setToRevokeMembers([...toRevokeMembers, dotYouId]);
      }
      setToProviderMembers(toProvideMembers.filter((defaultMember) => defaultMember !== dotYouId));
    }
  };

  const dialog = (
    <DialogWrapper size="4xlarge" onClose={onCancel} title={title}>
      <>
        <div>
          {members?.length && !membersLoading ? (
            members.map((member) => (
              <PersonCard
                key={member.dotYouId}
                name={
                  member.givenName && member.surName
                    ? `${member.givenName} ${member.surName}`
                    : member.dotYouId
                }
                className="relative w-1/2 p-1 sm:w-1/2 md:w-1/3 lg:w-1/4 xl:w-1/6"
                onClick={() => {
                  const isInDefault = defaultMembers.some(
                    (defaultMember) => defaultMember === member.dotYouId
                  );
                  const isRevoked = toRevokeMembers.some(
                    (defaultMember) => defaultMember === member.dotYouId
                  );
                  const isProvided = toProvideMembers.some(
                    (defaultMember) => defaultMember === member.dotYouId
                  );

                  const currentState = isInDefault ? !isRevoked : isProvided;
                  toggleMember(!currentState, member.dotYouId);
                }}
              >
                <Checkbox
                  className="absolute top-4 left-4"
                  onChange={(e) => {
                    toggleMember(e.target.checked, member.dotYouId);
                  }}
                  defaultChecked={
                    (defaultMembers.some((selectedMember) => selectedMember === member.dotYouId) &&
                      !toRevokeMembers.some(
                        (revokedMember) => revokedMember === member.dotYouId
                      )) ||
                    toProvideMembers.some((providedMember) => providedMember === member.dotYouId)
                  }
                />
              </PersonCard>
            ))
          ) : (
            <></>
          )}
        </div>
        <div className="-mx-2 py-3 sm:flex sm:flex-row-reverse">
          <ActionButton
            className="mx-2"
            icon={'send'}
            state={confirmationStatus}
            onClick={() => {
              onConfirm(toProvideMembers, toRevokeMembers);
            }}
          >
            {confirmText || t('Update Circle')}
          </ActionButton>
          <ActionButton className="mx-2" type="secondary" onClick={onCancel}>
            {t('Cancel')}
          </ActionButton>
        </div>
      </>
    </DialogWrapper>
  );

  return createPortal(dialog, target);
};

export default MemberLookupDialog;
